@import url(https://fonts.googleapis.com/css?family=Lato|Permanent+Marker);

.note {
    padding:10px 10px 38px 34px;
    height: auto; 
    margin: auto;
    margin-bottom: 8px;
    width: 60%;
    font-family: 'Lato', sans-serif;
    font-size: 1.4em;
    color: #222;
    background-color: rgb(214, 40, 69); 
    border-radius: 5px;
    box-shadow: 43x 3px 2px #333;
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  
  @keyframes fadeIn { from { opacity:0; } to { opacity:0.8; } }
  @-webkit-keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  @keyframes fadeOut { from { opactiy:0.8; } to { opacity:0; } }
  
  .fade-in {
      opacity:0;
      -webkit-animation:fadeIn ease-in 1;
              animation:fadeIn ease-in 1;
      -webkit-animation-fill-mode:forwards;
              animation-fill-mode:forwards;
      -webkit-animation-duration: 0.1s;
              animation-duration: 0.1s;
  }
  
  .noteContent{
    float:left;
    padding-right: 70px;
    padding-bottom: 10px;
    display: block;
  }
  
  .closebtn {
      color: #AA9;
      font-weight: bold;
      float: right;
      cursor: pointer;
      font-size: 1.3em;
  }
  
  .closebtn:hover {
      color: #AC2500;
  }
.formWrapper{
    margin: auto;
}

.noteButton{
    border: none;
    margin: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 8px 24px;
    font-size: 1.3em;
    font-family: 'Droid Sans', sans-serif;
    background-color: #019875;
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.noteButton:hover{
    background-color: #02BA85;
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.noteInput{
    width: 800px;
    min-width: 800px;
    font-size: 1.3em;
    padding-left: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-family: 'Droid Sans', sans-serif;
    background-color: rgba(34,49,63,0.8);
    color: #fff;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.noteInput:focus{
    background-color: #E4F1FE;
    color: #111;
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
}
html{
  height: 100%;
}

body {
  width: 100%;
  min-height:100%;
  background: 
    -webkit-linear-gradient(135deg, rgba(0,0,0,0.3), rgba(0, 40, 105, 0.8)),
    url(/PranshuMishra07/todo.git/static/media/attractive-beautiful-beauty-839347.03f7c8db.jpg);
  background: 
    linear-gradient(-45deg, rgba(0,0,0,0.3), rgba(0, 40, 105, 0.8)),
    url(/PranshuMishra07/todo.git/static/media/attractive-beautiful-beauty-839347.03f7c8db.jpg);
  background-attachment: fixed;
  background-size: cover; 
}

* {
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.notesWrapper {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  height: 100%;
}

.notesHeader {
  display: flex;
  flex: 1 1 5%;
  min-height: 2.5em;
  max-height: 2em;
  font-size: 3.2em;
  color: #F4B350;
  font-family: 'Permanent Marker', cursive;
  text-align: center;
  align-content: center;
  padding:20px 0px 20px 0px;
  margin-bottom: 6px;
  background-color: rgba(0,0,0,0.5);
  text-shadow: 3px 3px 10px #000;
  display: block;
}

.notesBody {
  padding-top: 24px;
  display: flex;
  flex: 1 1 85%;
  flex-wrap: wrap;
  align-items: flex-start;
  align-content: flex-start;
  overflow: auto;
}

.notesFooter {
  display: flex;
  flex: 1 1 7%;
  background-color: rgba(0,0,0,0.5);
}
